import * as React from 'react'
import { cva, type VariantProps } from 'class-variance-authority'
import { cn } from '@/app/shared/lib/utils'

const containerVariants = cva('mx-auto w-full', {
  variants: {
    variant: {
      default: 'max-w-7xl px-4 sm:px-6 lg:px-8',
      main: 'max-w-7xl px-4 sm:px-6 lg:px-8 py-8',
      full: 'px-0',
      narrow: 'max-w-4xl px-4 sm:px-6 lg:px-8',
      wide: 'max-w-8xl px-4 sm:px-6 lg:px-8',
    },
    size: {
      default: '',
      sm: 'max-w-2xl',
      md: 'max-w-4xl',
      lg: 'max-w-6xl',
      xl: 'max-w-7xl',
      '2xl': 'max-w-8xl',
    },
  },
  defaultVariants: {
    variant: 'default',
    size: 'default',
  },
})

export interface ContainerProps extends React.HTMLAttributes<HTMLDivElement>, VariantProps<typeof containerVariants> {}

const Container = React.forwardRef<HTMLDivElement, ContainerProps>(({ className, variant, size, ...props }, ref) => {
  return <div ref={ref} className={cn(containerVariants({ variant, size, className }))} {...props} />
})
Container.displayName = 'Container'

export { Container, containerVariants }
